<template>
  <div style="max-width: 100%;box-sizing: border-box" class="deviceApply">
    <myHeader
      :isBack="true"
      backPath="/business"
      title="设备申请"
      subtitle="记录"
      subtitlePath="/deviceApplyRecord">
    </myHeader>
    
    <div class="seat-head"></div>

    <div class="list-block">
      <mt-cell title="养户：" :value="fosterHouseholds"></mt-cell>
      <mt-cell title="申请日期：" @click.native="selectApplyDate">
        <span class="color-text">
          {{applyDateText}}
        </span>
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="累计已领设备金额：" :value="serviceMoney"></mt-cell>
    </div>

    <div class="service-apply-detail">
      <div class="top">
        <span>设备申请明细</span>
      </div>
      <div class="details-container" v-for="(item, index) in serviceApplyDetails" :key="index">
        <div class="detail-item">
          <div class="del-btn" @click="delApplyItem(index)"><img src="../../../assets/del-icon.png"></div>
          <div class="left">
            <span>设备名称：</span>
            <span v-if="item.serviceName"
                  @click="gotoSearchServiceName(index)">{{item.serviceName}}</span>
            <span class="tip-text" v-else @click="gotoSearchServiceName(index)">请选择名称</span>
          </div>
          <div class="right">
            <span>数量：</span>
            <input type="text"
                   v-model="item.serviceNum"
                   @input="handlerServiceNum(index,$event)"
                   placeholder="请输入数量" />
          </div>
        </div>
        <div class="detail-item">
          <div class="del-btn"></div>
          <div class="left">
            <span>计量单位：</span>
            <span>{{item.serviceCalculate}}</span>
          </div>
          <div class="right">
            <span>规格：</span>
            <span>{{item.serviceWeight}}</span>
          </div>
        </div>
        <div class="detail-item">
          <div class="del-btn"></div>
          <div class="left">
            <span>设备单价：</span>
            <span>{{item.serviceSinglePrice}}</span>
          </div>
          <div class="right">
            <span>金额：</span>
            <span>{{item.serviceMoney}}</span>
          </div>
        </div>
      </div>
      <div class="add-btn" @click="addApply">
        <span class="add-icon">+</span>新增
      </div>
    </div>

    <div class="select-type">
      <div class="title">
        是否委托他人领取
      </div>

      <div class="radio-select-box">
        <div class="radio-item">
          <span>否</span>
          <img v-if="isNeedOther" @click="switchNeedOtherHandler('isNeedOther')" src="../../../assets/select-icon.png">
          <img v-else @click="switchNeedOtherHandler('isNeedOther')" src="../../../assets/noSelect-icon.png">
        </div>
        <div class="radio-item">
          <span>是</span>
          <img v-if="isNotNeedOther" @click="switchNeedOtherHandler('isNotNeedOther')" src="../../../assets/select-icon.png">
          <img v-else @click="switchNeedOtherHandler('isNotNeedOther')" src="../../../assets/noSelect-icon.png">
        </div>
      </div>
      <div v-if="isNotNeedOther && isShowEntrust">
        <mt-cell title="委托领取人姓名：" @click.native="selectEntrust">
          {{clientName}}
          <span class="img"></span>
        </mt-cell>
        <mt-cell title="委托领取人身份证号：" :value="clientID"></mt-cell>
      </div>
    </div>
    
    <SubmitBtn @click.native.prevent="submitPageData()"
               :btnText="submitBtnText"
               :customStyle="myCustomStyle"></SubmitBtn>

    <!--选择申请日期-->
    <mt-datetime-picker
      ref="applyDatePicker"
      type="date"
      v-model="applyDate"
      @confirm="applyDateConfirm"></mt-datetime-picker>
  
    <div class="my-prop">
      <!--选择委托人-->
      <mt-popup
        v-model="switchEntrustPopup"
        position="bottom">
        <mt-picker :slots="needEntrustSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="entrustPicker">
          <span @click="handleEntrustConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
    </div>

  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import myHeader from '@/components/Header'
  import SubmitBtn from "@/components/SubmitBtn";
  import {queryEquipmentApplyInitData, queryEntrustList, saveEquipmentApply } from '@/api/materialApply'
  import { formatDate } from '@/utils/tool'
  import { inputLimitNum, submitValidator } from '@/utils/validator'
  import subMitBtnMixins from '@/mixins/subMitBtnMixins'

  export default {
    name: 'deviceApply',
    mixins: [subMitBtnMixins],
    data() {
      return {
        fosterHouseholds: '',
        applyDateText: formatDate(new Date(), 'yyyy-MM-dd'),
        applyDate: new Date(),
        serviceMoney: '',
        serviceApplyDetails: [
          {
            serviceName: '',
            serviceId: '',
            serviceNum: '',
            serviceCalculate: '',
            serviceWeight: '',
            serviceSinglePrice: '',
            serviceMoney: '',
            stock: ''
          },
        ],
        isNeedOther: true,
        isNotNeedOther: false,
        isShowEntrust: false,
        isEntrust: false,
        clientName: '',
        clientID: '',
        entrustorId: '',
        curSelectIndex: 0,
        switchEntrustPopup: false,
        needEntrustSlots: [{
          values: [
            {
              id: '', name: '',idcard: ''
            },
          ],
          textAlign: 'center'
        }]
      }
    },
    components: {
      SubmitBtn,
      myHeader
    },
    created() {
      this._queryEquipmentApplyInitData();
      this.fosterHouseholds = this.userName;
    },
    computed: {
      ...mapState({
        userName: state => state.user.userInfo.name
      })
    },
    beforeRouteEnter  (to, from, next) {
      next(vm => {
        
        if (from.name == 'searchServiceName' && sessionStorage.getItem('serviceInfo')) {
          let {materialName,model,price,unit, materialId, stock} = JSON.parse(sessionStorage.getItem('serviceInfo'));
          let newServiceMoney = price * stock;
          
          vm.$set(vm.serviceApplyDetails,vm.curSelectIndex,{
            serviceName: materialName,
            // serviceNum: vm.serviceApplyDetails[vm.curSelectIndex].serviceNum,
            serviceNum: stock,
            serviceCalculate: unit,
            serviceWeight: model,
            serviceSinglePrice: price,
            serviceId: materialId,
            serviceMoney: newServiceMoney,
            stock: stock
          });
  
        }
      });
    },
    beforeRouteLeave (to, from, next) {
      sessionStorage.removeItem('serviceInfo');
      next();
    },
    methods: {
      gotoSearchServiceName(index) {
        this.curSelectIndex = index;
        sessionStorage.removeItem('serviceInfo');
        this.$router.push('/searchServiceName');
      },
      _queryEquipmentApplyInitData() {
        queryEquipmentApplyInitData().then(res => {
          let data = res.data;
          this.serviceMoney = data.equipAmt;
        });
      },
      _queryEntrustList() {
        queryEntrustList().then(res => {
          let data = res.data;
          if (data.list.length) {
            this.isShowEntrust = true;
            this.needEntrustSlots[0].values = data.list.map(item => {
              return {
                name: item.name,
                id: item.id,
                idcard: item.idcard
              }
            });
  
            this.clientName = this.needEntrustSlots[0].values[0].name;
            this.clientID = this.needEntrustSlots[0].values[0].idcard;
            this.entrustorId = this.needEntrustSlots[0].values[0].id;
          } else {
            this.$toast({
              message: '无委托人信息，请维护养户档案！',
              duration: 1000
            });
            this.isNeedOther = true;
            this.isNotNeedOther = false;
            this.isEntrust = false;
            this.isShowEntrust = false;
          }
        }).catch(err => {
          this.isNeedOther = true;
          this.isNotNeedOther = false;
          this.isEntrust = false;
          this.isShowEntrust = false;
          this.$toast({
            message: '无委托人信息，请维护养户档案！',
            duration: 1000
          });
        });
      },
      selectApplyDate() {
        this.$refs.applyDatePicker.open();
      },
      applyDateConfirm(date) {
        this.applyDateText = formatDate(this.applyDate, 'yyyy-MM-dd');
      },
      handlerServiceNum(index,e) {
        if (this.serviceApplyDetails[index].serviceName == '') {
          this.serviceApplyDetails[index].serviceNum = '';
          this.$toast({
            message: `请先选择设备名称`,
            duration: 1000
          });
          return;
        }
        
        if (e.target.value > this.serviceApplyDetails[index].stock) {
          this.serviceApplyDetails[index].serviceNum = this.serviceApplyDetails[index].stock;
          this.$toast({
            message: `库存为${this.serviceApplyDetails[index].stock}不能大于`,
            duration: 1000
          });
          return;
        }
        
        this.serviceApplyDetails[index].serviceNum = inputLimitNum(e);
        
        if (this.serviceApplyDetails[index].serviceSinglePrice) {
          let newServiceMoney = this.serviceApplyDetails[index].serviceNum *
            this.serviceApplyDetails[index].serviceSinglePrice;
          
          this.$set(this.serviceApplyDetails,index,{
            serviceName: this.serviceApplyDetails[index].serviceName,
            serviceNum: this.serviceApplyDetails[index].serviceNum,
            serviceCalculate: this.serviceApplyDetails[index].serviceCalculate,
            serviceWeight: this.serviceApplyDetails[index].serviceWeight,
            serviceSinglePrice: this.serviceApplyDetails[index].serviceSinglePrice,
            serviceId: this.serviceApplyDetails[index].serviceId,
            serviceMoney: newServiceMoney ? newServiceMoney : '',
            stock: this.serviceApplyDetails[index].stock
          });
        }
      },
      delApplyItem(curIdx) {
        if (this.serviceApplyDetails.length > 1) {
          this.serviceApplyDetails = this.serviceApplyDetails.filter((item, idx) => {
            return curIdx !== idx;
          });
        }
      },
      addApply() {
        this.serviceApplyDetails.push({
          serviceName: '',
          serviceNum: '',
          serviceId: '',
          serviceCalculate: '',
          serviceWeight: '',
          serviceSinglePrice: '',
          serviceMoney: '',
          stock: ''
        });
      },
      switchNeedOtherHandler(type) {
        if (type === 'isNeedOther') {
          if (!this.isNeedOther) {
            this.isNeedOther = true;
            this.isNotNeedOther = false;
            this.isEntrust = false;
          }
        } else if (type === 'isNotNeedOther') {
          if (!this.isNotNeedOther) {
            this.isNotNeedOther = true;
            this.isNeedOther = false;
            this.isEntrust = true;
            this._queryEntrustList();
          }
        }
      },
      selectEntrust() {
        if (this.needEntrustSlots[0].values[0].name) {
          this.switchEntrustPopup = !this.switchEntrustPopup;
        } else {
          this.$toast({
            message: '无委托人信息，请维护养户档案！',
            duration: 1000
          });
        }
      },
      handleEntrustConfirm() {
        this.clientName = this.$refs.entrustPicker.getValues()[0].name;
        this.clientID = this.$refs.entrustPicker.getValues()[0].idcard;
        this.entrustorId = this.$refs.entrustPicker.getValues()[0].id;
        this.switchEntrustPopup = false;
      },
      checkSubmitData() {
        let checkList = [];
        for (let i = 0 ; i < this.serviceApplyDetails.length; i++) {
          checkList.push({
            val: this.serviceApplyDetails[i].serviceName,
            rule: ['required'],
            getTipText: '设备名称'
          });
          checkList.push({
            val: this.serviceApplyDetails[i].serviceNum,
            rule: ['required'],
            getTipText: '设备数量'
          });
        }
        
        let flag = submitValidator(checkList);
        return flag;
      },
      _saveEquipmentApply() {
        let changeList = this.serviceApplyDetails.map(item => {
          return {
            materialId: item.serviceId,
            materialName: item.serviceName,
            qty: item.serviceNum,
            model: item.serviceWeight,
            price: item.serviceSinglePrice,
            unit: item.serviceCalculate,
            amount: item.serviceMoney
          }
        });
        
        const data = {
          custName: this.fosterHouseholds,
          bizDate: this.applyDateText,
          totalPayment: this.serviceMoney,
          entrys: changeList,
          isEntrust: this.isEntrust,
          entrustorName: this.clientName,
          entrustorId: this.entrustorId,
          entrustorIdCard: this.clientID
        };
        
        saveEquipmentApply(data).then((res)=>{
          this.$toast({
            message: '提交成功！',
            duration: 1000
          });
          this.$router.push('/deviceApplyRecord');
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        }).catch((err)=>{
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        })
      },
      submitPageData() {
        let flag = this.checkSubmitData();
        if (!flag) return false;
        
        if (!this.isDisabledSubmit) {
          this.isDisabledSubmit = true;
          this.submitBtnText = '提交中...';
          this._saveEquipmentApply();
        }
        
      }
    }
  }
</script>

<style lang="scss">
  .deviceApply {
    min-height: 100vh;
    background-color: #F8F8F8;

    .head {
      background-color: #FFF;
    }

    .list-block {
      font-size: .16rem;
      margin-top: .1rem;
      background-color: #fff;
      .img {
        width: .1rem;
        height: .06rem;
        margin-left: .08rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
      }
    }
    
    .service-apply-detail {
      font-size: .16rem;
      background-color: #fff;
      margin-top: .1rem;
      .top {
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .details-container {
        padding-left: .2rem;
        border-bottom: 1px solid #EBEBEB;
        
        .detail-item {
          padding-right: .15rem;
          font-size: .14rem;
          display: flex;
          height: .54rem;
          justify-content: space-between;
          line-height: .54rem;
          border-bottom: 1px solid #EBEBEB;
          .del-btn {
            width: 5%;
          }
          .left {
            width: 50%;
            padding-left: 2.5%;
            .tip-text {
              color: #888;
            }
          }
          .right {
            width: 40%;
            padding-left: 2.5%;
          }
          input {
            width: .73rem;
            font-size: .14rem;
          }
          
          &:last-child {
            border-bottom: 0;
          }
        }
      }
  
      .add-btn {
        font-size: .12rem;
        color: #888;
        height: .3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #EBEBEB;
        .add-icon {
          margin-right: .1rem;
        }
      }
    }

    .apply-detail {
      font-size: .16rem;
      background-color: #fff;
      margin-top: .1rem;
      .top {
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      .detail-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: .54rem;
        padding: 0 .35rem;
        border-bottom: 1px solid #dddddd;
        font-size: .14rem;
        .item-name {
          width: 70%;
        }
        .item-num {
          width: 30%;
        }
      }
    }

    .select-type {
      background-color: #fff;
      margin-top: .1rem;
      font-size: .16rem;
      .title {
        height: .45rem;
        padding: .1rem .15rem;
        border-bottom: 1px solid #dddddd;
      }
      .radio-select-box {
        padding-left: .15rem;
        border-bottom: 1px solid #dddddd;
        .radio-item {
          display: flex;
          justify-content: space-between;
          height: .52rem;
          line-height: .52rem;
          padding-left: .05rem;
          padding-right: .15rem;
          &:first-child {
            border-bottom: 1px solid #dddddd;
          }
          img {
            align-self: center;
          }
        }
      }
      .img {
        width: .1rem;
        height: .06rem;
        margin-left: .08rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
      }
    }
  
  
    .my-prop {
      .mint-popup {
        width: 100%;
        height: 25%;
      }
  
      /deep/ .picker-toolbar {
        height: 0;
      }
  
      .sure-btn {
        position: absolute;
        top: .05rem;
        right: .05rem;
        padding: .05rem .20rem;
        font-size: .16rem;
        z-index: 99;
      }
    }
  }
</style>



